
import {host,judgeLogin,toast,navigate,LoginPath,navigateBack} from '@/utils'


  // get
  // statusObj ---isLogin
  export  function get(url, data={},statusObj={}) {
    if((statusObj.isLogin)&&!judgeLogin()){
      return Promise.reject()
    }else{
      return request(url, 'GET', data,statusObj)
    }
  }
  // post
export function post(url, data={},statusObj={}) {
    if((statusObj.isLogin)&&!judgeLogin()){
      return Promise.reject()
    }else{
      return request(url, 'POST', data,statusObj)
    }
  }

  
let loginTipsStatus = false;//
// code
const code={
  success:0,//
  fail:1,//
  notLogin:2,//||
  resCode1:200,//
}

//
function request(url, method, data) {
    uni.showLoading({
      title: '' //loading
    })
    return new Promise((resolve, reject) => { 
      uni.request({
        url: host + url, //
        method: method,
        data: data,
        header: {
          'content-type': 'application/json;charset=utf-8' // 
        },
        success: function (res) {
          uni.hideLoading();
          if(res.statusCode===200){
            const ret = res.data;
            switch (ret.code) {
              case code.success:
                resolve(ret);
                break;
              case code.resCode1:
                resolve(ret);
                break;
              case code.notLogin:
                // 
                uni.setStorageSync("userId",'')
                uni.setStorageSync("token",'')
                if(!loginTipsStatus){
                    loginTipsStatus = true;
                    
                    uni.showModal({
                      title:'',
                      content:'',
                      cancelColor:'#999',
                      confirmColor:'#ff6f00',
                      success(res){
                        if(res.confirm){
                          navigate(LoginPath)
                        }
                      },
                      complete(){
                        loginTipsStatus = false;
                      }
                    })
                  }
                break;
              case code.fail:
                toast(ret.msg)
                reject(ret)
                break;
              default:
                toast(ret.msg)
                reject(ret)
            }
          }else{
            toast('')
            navigateBack();
            reject()
          }
        },
        fail: function (error) {
          uni.hideLoading();
          toast('')
          navigateBack();
          reject(error)
        }
      })
    })
  }
  
//,
export function requestHideLoading(url, data,method) {
    return new Promise((resolve, reject) => {
      uni.request({
        url: host + url, //
        method: method,
        data: data,
        header: {
          'content-type': 'application/json;charset=utf-8' // 
        },
        success: function (res) {
          const ret = res.data;
          if(code.success){
             resolve(ret)
         }else{
              toast(ret.msg)
              reject(ret)
         }
        },
        fail: function (error) {
          toast('')
          reject(error)
        }
      })
    })
  }

