// icon--mask--
export function toast(title,data={icon:false,mask:false,time:2000}){
  uni.showToast({
    title:title,
    icon:data.icon?'success':'none',
    mask:data.mask||false,
    duration:data.time||2000
  })
}
//
export function judgeLogin(){
  // falsetrue
  if (!uni.getStorageSync("userId") || !uni.getStorageSync("token")) {
      uni.showModal({
        title:'',
        content:'',
        cancelColor:'#999',
        confirmColor:'#ff6f00',
        success(res){
          if(res.confirm){
            navigate(LoginPath)
          }
        }
      })
      return false;
  }else{
    return true;
  }
}
// 
export function trim(str) {
  return str.replace(/(^\s*)|(\s*$)/g, "");
}
//
export function verifyPhone(tel) {
  var r_phone = /^1(3|4|5|6|7|8|9)\d{9}$/;
  // var phoneNumber = $.trim($('#phoneNumber').val());
  if (trim(tel) == "") {
    toast( "!");
    return false;
  }
  if (!r_phone.test(tel)) {
    toast("!");
    return false;
  }
  return true;
}

// ,
let timeout = null
export function debounce(fn, wait = 500) {
  if (timeout !== null) clearTimeout(timeout)
  timeout = setTimeout(fn, wait)
}
// ,
let throtteStatus = false
export function throtte(fn, wait = 500) {
  if (throtteStatus) return;
  throtteStatus = true;
  setTimeout(fn, wait)
  setTimeout(() => {
    throtteStatus = false;
  }, wait)
}
// 
export function upFile(filePath) {
  return new Promise((resolve, reject) => {
    uni.uploadFile({
      url: host + 'Area/VoiceUpload',
      filePath,
      name: 'file',
      success(res) {
        console.log('', res)
        resolve(res);
      },
      fail(err) {
        console.log('', err)
        uni.showToast({
          title: '',
          icon: 'none'
        })
        reject(err)
      }
    })
  })
}
// 
export function saveFile(filePath){
  // downloadFilesaveFile
  uni.downloadFile({   
     url: filePath,   
     success: (res) => {  
        if (res.statusCode === 200) { 
          // h5
		      if (typeof window === 'object' && 'document' in window) {
            var oA = document.createElement("a");
            oA.download = '';// ''
            oA.href = res.tempFilePath;
            document.body.appendChild(oA);
            oA.click();
            oA.remove(); // 
          }else{
            uni.saveFile({  
                 tempFilePath: res.tempFilePath,  
                 complete(res) {  
                   console.log(res)  
                 }  
               });
          }  
        }  
     }  
 });
}

// 
export function autoImg(img) {
  if (img.indexOf('http') === -1) {
    return filePath + img
  }
  return img;
}

// ,
export function navigateBack(){
  debounce(function(){uni.navigateBack();console.log('')},2000)
}
// url,
export function navigate(url,params,isLogin){
  console.log(''+url)
  // 
  if(isLogin&&!judgeLogin()){
    return;
  }
  let p ='';
  if(params){
    let arr = Object.keys(params);
    arr.map((item,index)=>{
      p+=`${item}=${params[item]}`;
      if(index<arr.length-1){p+='&'};
    })
  }
  uni.navigateTo({
    url:`/pages/${url}?${p}`
  })
}
// url,
