 import {toast} from '@/utils';
 import QQMapWX from "@/utils/qqmap-wx-jssdk"; //reverseGeocoder
 let qqmapsdk = new QQMapWX({
     key: "LJTBZ-A5SWU-ZDGVJ-2OV5E-SOKAJ-HIBQI" // 
 });
 // 
 export function hasPosition() {
     return new Promise((resolve, reject) => {
         uni.getSetting({
             success: res => {
                 console.log("*****1111111111*******", res);
                 // res.authSetting['scope.userLocation'] == undefined     
                 // res.authSetting['scope.userLocation'] == false     ,
                 // res.authSetting['scope.userLocation'] == true     

                 //
                 if (
                     res.authSetting["scope.userLocation"] != undefined &&
                     res.authSetting["scope.userLocation"] != true
                 ) {
                     toast('')
                     reject();
                 } else if (res.authSetting["scope.userLocation"] == undefined) {
                     //,wx.getLocationAPI
                     console.log("******222222222******");
                     getMyPosition().then(res=>{
                        resolve(res);
                     });
                 } else {
                     console.log("");
                     //wx.getLocationAPI
                     getMyPosition().then(res=>{
                        resolve(res);
                     });
                 }
             }
         });

     })
 }
 // 
 export function initPosition() {
     return new Promise((resolve,reject)=>{
        uni.openSetting({
            success: function (res) {
                console.log(res, "");
                if (res.authSetting["scope.userLocation"] == true) {
                    //wx.getLocationAPI
                    getMyPosition().then(res=>{
                    resolve(res);
                    });
                } else {
                    toast('')
                    reject();
                }
            },
            fail(err) {
                console.log(err, 'err')
                reject();
            }
        });
    })
 }
 // 
 export function getMyPosition() {
     return new Promise((resolved, rejected) => {
         wx.getLocation({
             type: "gcj02",
             success: data => {
                 console.log(data, this, "");
                 let lat = data.latitude;
                 let lng = data.longitude;
                 // 
                 getCityinfo(lat, lng).then((res) => {
                     res = Object.assign(res, {
                         lat,
                         lng
                     })
                     console.log(res, 'return')
                     resolved(res);
                 });
             },
             fail(err) {
                 console.log(err);
                 toast("");
                 rejected();
             }
         });
     });
 }
 //nowPlace 
 export function getCityinfo(lat, lng) {
     return new Promise((resolved, rejected) => {
         qqmapsdk.reverseGeocoder({
             location: {
                 latitude: lat,
                 longitude: lng
             },
             success(res) {
                 // console.log(res,'qqmap')
                 // const _res = res.result;
                 // that.cityName = _res.address_component.city;
                 // that.update({
                 //   cityName: _res.address_component.city,
                 //   nowPlace:
                 //     _res.formatted_addresses.recommend + " - " + _res.address
                 // });
                 resolved(res.result);
             },
             fail: function (err) {
                 toast("");
                 rejected(err);
             }
         });
     });
 }