function valPhone(tel) {
	var r_phone = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
	// var phoneNumber = $.trim($('#phoneNumber').val());
	if(tel == "") {
		uni.showToast({
          title: "!",
          icon: "none",
          duration: 2000
        });
		return false;
	}
	if(!r_phone.test(tel)) {
		uni.showToast({
		  title: "!",
		  icon: "none",
		  duration: 2000
		});
		return false;
	}
	return true;
}

function createTag(tagName,name,content){
	let tag = document.createElement(tagName);
	tag.name = name;
	tag.content = content;
	return tag;	
}
let doctit="";
function SEOTitle(str){	
	if(str==""){
		document.title=doctit;
	}else{
		document.title= str+"-"+doctit;
	}
}










function getUrlParam(name) {
	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
	var url = window.location.search.substr(1); //.toLowerCase();
	var r = url.match(reg);
	if(r != null) return unescape(r[2]);
	return null;
}
// 
function navigate(url,params={}){
	let p ='';
	let arr = Object.keys(params);//
	arr.map(item=>{
		p+=`${item}=${params[item]}`;
		if(arr[arr.length-1]!==item){
			p+='&';
		}
	})
	uni.navigateTo({
		url:'/pages/'+url+(p&&('?'+p))
	})
}
function uncodeUtf16(str){
  var reg = /\&#.*?;/g;
  var result = str.replace(reg,function(char){
  	 var H,L,code;
  if(char.length == 9 ){
  code = parseInt(char.match(/[0-9]+/g));
  H = Math.floor((code-0x10000) / 0x400)+0xD800;
  L = (code - 0x10000) % 0x400 + 0xDC00;
  return unescape("%u"+H.toString(16)+"%u"+L.toString(16));
  }else{
  return char;
  }
  });
  return result;
 }
